import React, { useState } from "react";
import axios from 'axios';
import AceEditor from 'react-ace';

import "brace/ext/language_tools";
import "brace/ext/searchbox";
import 'brace/mode/python';
import 'brace/theme/monokai';
import 'brace/theme/kuroir';

export default () => {
  const compileURL = 'https://regmi-solutions.de/python/api.php';
  const [input, setInput] = useState('');
  const [output, setOutput] = useState('# Output');
  const [showLoading, setShowLoading] = useState(false);

  const onCodeChange = async(code) => {
    setInput(code);
    if (code) {
      try {
        setShowLoading(true);
        const result = await axios.get(compileURL, {
          params: {
            apikey: 'shankarboro',
            code
          }
        }).then(response => response.data);
        setOutput(`#Output \n${result}`);
        setShowLoading(false);
      } catch (error) {
        setShowLoading(false);
      }
    } else {
      setOutput('# Output');
    }

  }
  return (
    <div className="row">
      <div className="col-md-7 no-padding">
        <AceEditor
          focus
          name="editorInput"
          mode="python"
          theme="monokai"
          width="100%"
          height="100vh"
          debounceChangePeriod={2000}
          fontSize={14}
          tabSize={2}
          value={input}
          onChange={onCodeChange}
          editorProps={{ $blockScrolling: Infinity }}
        />
      </div>
      <div className="col-md-5 no-padding">
        <AceEditor
          value={output}
          readOnly
          name="codeOutput"
          mode="python"
          theme="kuroir"
          width="100%"
          height="100vh"
          showGutter={false}
          fontSize={14}
          tabSize={2}
          editorProps={{ $blockScrolling: Infinity }}
        />
      </div>
      {
        showLoading && <i className="fas fa-spinner fa-spin float-right"></i>
      }
    </div>
  );
}
